import React from 'react';
import d from './Dialogs.module.css';
import DialogItem from "./DialogItem/DialogItem";
import Message from "./Message/Message";
import {Field, Form, reduxForm} from "redux-form";
import {Textarea} from "../common/FormControls/FormControls";
import {maxLengthCreater, required} from "../../utils/validators/validators";

let maxLength200 = maxLengthCreater(200);
const MessageAddForm = (props) => {
    return <Form onSubmit={props.handleSubmit} >
        <Field
            // className={d.textArea}
               component={Textarea}
               validate={[required, maxLength200]}
               name='newMessageAdd'
               placeholder={'New Message'}/>
        <div>
            <button className={d.addButton}>Add Message</button>
        </div>
    </Form>

}
let WithMessageAddForm = reduxForm({form: 'dialogAddMessage'})(MessageAddForm)

const Dialogs = (props) => {
    let dialogElements = props.messagePage.dialogs.map(d => (
        <DialogItem name={d.name} id={d.id}/>
    ))
    let messageElements = props.messagePage.messages.map(m => (
        <Message message={m.message}/>
    ))

    let onAddMessage = (values) => {
        props.addMessage(values.newMessageAdd)
    }
    return <div className={d.dialogs}>
        <div className={d.dialogItems}>
            {dialogElements}
        </div>
        <div className={d.messages}>
            {messageElements}
            <WithMessageAddForm onSubmit={onAddMessage}/>
        </div>
    </div>
}
export default Dialogs;


